.card-container {
	display: flex;
	flex-direction: column;
	background-color: #95dada;
	border: 1px solid grey;
	border-radius: 5px;
	padding: 25px;
	cursor: pointer;
	transform: scale(1);
	transition: transform 0.25s ease-out;
}

.card-container:hover {
	transform: scale(1.05);
}

.card-container p {
	word-break: break-all;
}